import {
  PARTIE,
  glosy2019,
  okregi,
  sondaze,
  srednia,
  sredniaSondazy,
} from "./dane";
import {
  Wynik,
  Sondaze,
  ILE_SYMULACJI,
  Okreg,
} from "./typy";
import {
  rysujWynikWyborow,
} from "./wykresy";
import {
  NaDemo,
  NaPolDemo,
  NormalizujWynik,
  PodzialMandatowKraj,
  PodzialMandatowWOkregu,
  PoparcieWOkregu,
  losowanieWyniku,
} from "./ordynacja";
import { komentarze } from "./komentarz";

const sondazePolDemo = {};
Object.entries(sondaze).forEach(([pracownia, wynik]) => {
  sondazePolDemo[pracownia] = NaPolDemo(wynik as Wynik);
});
rysujWynikWyborow( { średnia: NaPolDemo(srednia), ...sondazePolDemo }, "sondaze", { laczOpozycje: false });

const wynikiWgSondazy: Sondaze = {};
wynikiWgSondazy["średnia"] = NaPolDemo(PodzialMandatowKraj(sredniaSondazy));
Object.entries(sondaze).forEach(
  ([sondaz, wynik]) =>
    (wynikiWgSondazy[sondaz] = NaPolDemo(PodzialMandatowKraj(wynik as Wynik)))
);


rysujWynikWyborow(wynikiWgSondazy, "mandatyKraj", {
  poziomo: true,
  laczOpozycje: false, 
  calySejm: true,
});

const podzialWOkregach: Sondaze = {};
const podzialWOkregachDemo: Sondaze = {};
const symulacje = document.createElement("p");
symulacje.setAttribute("class", "symulacje");
let partieString = '';
for (const partia of PARTIE) {
  partieString += `<td>${partia}</td>`;
}
let tabelka = "<table>";
tabelka += `<tr><th colspan="3">Okręg wyborczy</th><th colspan="7">Prognoza ilości głosów (na komitety powyżej progu)</th><th colspan="5">Progonoza podziału mandatów</th><th colspan="5">Głosy powyżej ceny mandatu</th><th colspan="5">Głosy brakujące do następnego mandatu</th></tr>`;
tabelka += `<tr><th>nr</th><th>Nazwa</th><th>Mandaty</th><th>Suma</th>${partieString}<th>Cena mandatu</th>${partieString}${partieString}${partieString}`;

tabelka +='</tr>';
const sumaGlosow: Wynik = { KO: 0, PIS: 0, LEWICA: 0, TD: 0, KONF: 0 };
const sumaMandatow: Wynik = { KO: 0, PIS: 0, LEWICA: 0, TD: 0, KONF: 0 };
const altSumaMandatow: Wynik = { KO: 0, PIS: 0, LEWICA: 0, TD: 0, KONF: 0 };
const sumaReszt: Wynik = { KO: 0, PIS: 0, LEWICA: 0, TD: 0, KONF: 0 };
const altSumaReszt: Wynik = { KO: 0, PIS: 0, LEWICA: 0, TD: 0, KONF: 0 };
okregi.forEach((okreg) => {
  const wynikWOkregu = NormalizujWynik(PoparcieWOkregu(sredniaSondazy, okreg).wynikWOkregu);
  const podzialMandatow = PodzialMandatowWOkregu(
      wynikWOkregu,
      okreg
  );
  const alternatywnyWynik = {
    PIS: wynikWOkregu.PIS,
    KONF: wynikWOkregu.KONF,
  };
  const PARTIE_OPOZYCJI = ["KO", "TD", "LEWICA"];
  const PRZESUNIETE_GLOSY_PROCENT = 0.3;
  const rekomendacje = komentarze[okreg.miasto].rekomendacjePartii || [];
  for (const partia of rekomendacje) {
    // dodajemy 0.1% lub 0.05% do wyniku odpowiednio jednej lub dwoch rekomendowanych partii
    alternatywnyWynik[partia] =
      wynikWOkregu[partia] +
      PRZESUNIETE_GLOSY_PROCENT /
        rekomendacje.length;
  }
  if (rekomendacje.length) {
    for (const partia of PARTIE_OPOZYCJI) {
      // odejmujemy 0.1% lub 0.05% od wyniku odpowiednio innych rekomendowanych partii
      if (!rekomendacje.includes(partia)) {
        alternatywnyWynik[partia] =
          wynikWOkregu[partia] -
          PRZESUNIETE_GLOSY_PROCENT /
            (PARTIE_OPOZYCJI.length -
              rekomendacje.length);
      }
    }
  } else {
    for (const partia of ["KO", "TD", "LEWICA"]) {
      alternatywnyWynik[partia] = wynikWOkregu[partia];
    }
  }

  const alternatywnyPodzialMandatow = PodzialMandatowWOkregu(
      alternatywnyWynik,
      okreg
  );
  podzialWOkregach[okreg.miasto] = NaPolDemo(podzialMandatow);
  podzialWOkregachDemo[okreg.miasto] = NaDemo(podzialMandatow);

  // Tabelka
  const glosy = glosy2019[okreg.nr - 1].reduce((accumulator: number, currentValue: number): number => {
    return accumulator + currentValue;
  }, 0);
  const glosyFn = (procent) => Math.round(glosy * procent / 100);
  const resztaRawFn = (procent) =>
    Math.round(((glosy * procent) / 100) % podzialMandatow.cenaMandatu);
  // Mamy błędy zaokrąglenia do około 50 głosów, dlatego odejmujemy 4 i robimy z tego niezerową liczbę.
  const resztaFn = (procent) =>
    Math.max(0, ((resztaRawFn(procent) + 8) % podzialMandatow.cenaMandatu) - 10);
  const brakujacaFn = (procent) => resztaFn(procent) ? podzialMandatow.cenaMandatu - resztaFn(procent) : 0;
  tabelka += `<tr><td>${okreg.nr}</td><td>${okreg.miasto}</td><td>${okreg.mandaty}</td><td>${glosy}</td>`;
  for (const partia of PARTIE) {
    const glosy = glosyFn(wynikWOkregu[partia]);
    sumaGlosow[partia] += glosy;
    tabelka += `<td>${glosy}</td>`;
  }

  tabelka += `<td>${podzialMandatow.cenaMandatu}</td>`;

  const altResztaRawFn = (procent) =>
    Math.round(((glosy * procent) / 100) % alternatywnyPodzialMandatow.cenaMandatu);
  // Mamy błędy zaokrąglenia do około 50 głosów, dlatego odejmujemy 4 i robimy z tego niezerową liczbę.
  const altResztaFn = (procent) =>
    Math.max(0, ((altResztaRawFn(procent) + 8) % alternatywnyPodzialMandatow.cenaMandatu) - 10);
  
  for (const partia of PARTIE) {
    sumaMandatow[partia] += podzialMandatow[partia] || 0;
    tabelka += `<td>${podzialMandatow[partia] || 0}</td>`;
  }
  
  for (const partia of PARTIE) {
    const reszta = resztaFn(wynikWOkregu[partia]);
    sumaReszt[partia] += reszta;
    tabelka += `<td>${reszta}</td>`;
  }

  for (const partia of PARTIE) {
    const brakujace = brakujacaFn(wynikWOkregu[partia]);
    tabelka += `<td>${brakujace}</td>`;
  }

  tabelka += `</tr>`;
  
  // Wiersz alternatywny
  tabelka += `<tr><td colspan="4">Podział przy przesunięciu ${PRZESUNIETE_GLOSY_PROCENT}% głosów:</td>`;
  for (const partia of PARTIE) {
    const glosy = glosyFn(alternatywnyWynik[partia]);
    tabelka += `<td>${glosy}</td>`;
  }
  tabelka += `<td>${alternatywnyPodzialMandatow.cenaMandatu}</td>`;
  for (const partia of PARTIE) {
    altSumaMandatow[partia] += alternatywnyPodzialMandatow[partia] || 0;
    tabelka += `<td>${alternatywnyPodzialMandatow[partia] || 0}</td>`;
  }
  for (const partia of PARTIE) {
    const reszta = altResztaFn(alternatywnyWynik[partia]);
    altSumaReszt[partia] += altResztaFn(alternatywnyWynik[partia]);
    tabelka += `<td>${reszta}</td>`;
  }
  tabelka += `</tr>`;
});


tabelka += `<tr><td colspan="3">Suma</td>`;
tabelka += `<td>${Object.values(sumaGlosow).reduce((acc, cur) => { return acc += cur}, 0)}</td>`;
for (const partia of PARTIE) {
  tabelka += `<td>${sumaGlosow[partia]}</td>`;
}
tabelka += `<td>-</td>`;
for (const partia of PARTIE) {
  tabelka += `<td>${sumaMandatow[partia]}</td>`;
}
for (const partia of PARTIE) {
  tabelka += `<td>${sumaReszt[partia]}</td>`;
}
tabelka += `</tr><tr>`;

tabelka += `<td colspan="10">Alternatywny wynik wg. rekomendacji</td>`;
for (const partia of PARTIE) {
  tabelka += `<td>${altSumaMandatow[partia]}</td>`;
}
for (const partia of PARTIE) {
  tabelka += `<td>${altSumaReszt[partia]}</td>`;
}
tabelka += `</tr>`;

symulacje.innerHTML = tabelka + "</table>";
document.getElementById("tabela").appendChild(symulacje);


rysujWynikWyborow(podzialWOkregach, "mandatyWOkregach", {
    poziomo: true,
    laczOpozycje: false,
});
rysujWynikWyborow(podzialWOkregachDemo, "mandatyWOkregachDemo", {
    poziomo: true,
    laczOpozycje: true,
});

RozkladResztWOkregach(sredniaSondazy);

export function RozkladResztWOkregach(
  wynik: Wynik
) {  
  for (let i = 1; i <= 100; i++) {
    const sumaReszt: Wynik = { KO: 0, PIS: 0, LEWICA: 0, TD: 0, KONF: 0 };
    for (const okreg of okregi) {
      const { wynikWOkregu, odchylenieWOkregu } = PoparcieWOkregu(wynik, okreg);
      const procentyWOkreguSrednia = NormalizujWynik(wynikWOkregu);
      const bucketFn = (num, bucket) => Math.round(num * bucket) / bucket;

      // Losowanie poparcia dla partii
      let { procentWOkreguLosoweUncapped, procentWOkreguLosoweCapped } =
        losowanieWyniku(procentyWOkreguSrednia, odchylenieWOkregu, bucketFn);
      // Podział mandatów
      const mandatyWOkregu = PodzialMandatowWOkregu(
        procentWOkreguLosoweUncapped,
        okreg
      );
      const glosy = glosy2019[okreg.nr - 1].reduce(
        (accumulator: number, currentValue: number): number => {
          return accumulator + currentValue;
        },
        0
      );
      const glosyFn = (procent) => Math.round((glosy * procent) / 100);
      const resztaRawFn = (procent) =>
        Math.round(
          ((glosyFn(procent)) / 100) % mandatyWOkregu.cenaMandatu
        );
      // Mamy błędy zaokrąglenia do około 50 głosów, dlatego odejmujemy 4 i robimy z tego niezerową liczbę.
      const resztaFn = (procent) =>
        Math.max(
          0,
          ((resztaRawFn(procent) + 8) % mandatyWOkregu.cenaMandatu) - 10
        );
        console.log(procentWOkreguLosoweUncapped, mandatyWOkregu);
      for (const partia of PARTIE) {
        const glosyPartia = glosyFn(procentWOkreguLosoweUncapped[partia]);
        const reszta = resztaFn(glosyPartia);
        console.log(partia, glosyPartia, reszta);
        sumaReszt[partia] += reszta || 0;
        tabelka += `<td>${reszta}</td>`;
      }
    }
    console.log(sumaReszt);
  }
}